.color-picker {
  position: absolute;
  left: -200px; /* Adjust this value to the width of the color picker */
  top: 10px;
  transition: left 0.3s ease; /* Animation for sliding effect */
  z-index: 10;
}

.color-picker.slide-in {
  left: 10px; /* Final position when shown */
}
